<template lang="html">
  <div class="collectionSelector-view container " style="margin-bottom:32px">
    <slot></slot>
    <center><button type="button" class="btn my-btn-orange" :class="{ 'my-btn-orange-active': collection === 'All' }" @click="changeCollection('All')">{{language.all}}</button>
    <button type="button" class="btn my-btn-orange" v-for="(item,index) in this.userDB.collections"  v-if="(userDB.collections.length !== 0 || checkboxMode === true)" :class="{ 'my-btn-orange-active': index === collection || (typeofEdit === 'add' && index === 0) }" @click="changeCollection(index)">{{index == 0 ? language.withoutColl : item.name}}</button>

    <router-link tag="button" type="button" class="btn my-btn-confirm" v-if="userDB.collections.length === 1 && !fromEditor" to="collections">{{language.btnAddColl}}</router-link>
    <button type="button" class="btn my-btn-confirm" v-if="fromEditor" @click="toggleMode('addMode')">{{language.btnAddColl}}</button>
    <button type="button" class="btn my-btn-confirm" v-if="fromLearning" @click="startLearning(collection)">{{language.btnStart}}</button>
    <button type="button" class="btn my-btn-confirm" v-if="fromTest" @click="startChecking(collection)">{{language.btnStart}}</button>
  </center>
  </div>
</template>

<script>
import SMmixin from '../mixins/StateManagment.js'
export default {
  mixins: [SMmixin],
  props: ['fromEditor', 'fromLearning', 'fromTest'],
  methods: {
    startLearning (collection) {
      collection === 'All'
        ? this.$store.commit('setState', ['cards', this.allwords])
        : this.$store.commit('setState', ['cards', this.userDB.collections[collection].knowledges])
      if (this.cards.length > 0) this.$store.commit('setState', ['learningMode', true]); else alert('Collection is empty')
      this.$store.commit('setState', ['testMode', false])
    },
    startChecking (collection) {
      collection === 'All'
        ? this.$store.commit('setState', ['cards', this.allwords])
        : this.$store.commit('setState', ['cards', this.userDB.collections[collection].knowledges])
      if (this.cards.length > 0) this.$store.commit('setState', ['testMode', true]); else alert('Collection is empty')
      this.$store.commit('setState', ['learningMode', false])
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~bootstrap/scss/_functions';
@import '~bootstrap/scss/_variables';
@import '~bootstrap/scss/mixins/_breakpoints';

div.container{
  button{
    margin-right: 4px;
    margin-top: 4px;
    max-width: 100%;
    white-space:pre-line;
    word-wrap:break-word !important;
  }
  button:first-child{
    margin-right: 11px;
  }
}
.asd{
  background-color: #000
}
</style>



