<template lang="html">
  <div class="learning-view" >
    <navbar></navbar>
    <jumbotron ><center><h1 class="display-4">{{language.learning}}</h1></center></jumbotron>
    <collselector :fromLearning="true" v-if="!learningMode"><p class="display-5" style="text-align:center">{{language.learningStart}}</p></collselector>

    <card class="" v-if="learningMode" :currentCard='currentCard'>
      <center><button :data-toggle="finished ? 'modal' : null" data-target="#myModal" type="button" class="btn my-btn-confirm" @click="next()">{{language.btnGotIt}}<i class="fa fa-long-arrow-right" aria-hidden="true" style="margin-left:5px"></i></button></center>
    </card>

    <div class="popup" @click='popuped()'>
      <div class="modal fade" id="myModal">
        <div class="modal-dialog" data-dismiss="modal">
          <div class="modal-content">

            <!-- Modal Header -->
            <div class="modal-header">
              <h4 class="modal-title">{{language.popupHead1}} &#171;{{ collection !== 'All' ? userDB.collections[collection].name : 'All'  }}&#187; {{language.popupHead2}}</h4>
            </div>

            <!-- Modal body -->
            <div class="modal-body">
              {{language.popupBody}}
            </div>

            <!-- Modal footer -->
            <div class="modal-footer">
              <button type="button" class="btn my-btn-orange" >{{language.btnOk}}</button>
            </div>

          </div>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
import SMmixin from '../../mixins/StateManagment.js'
export default {
  mixins: [SMmixin],
  data () {
    return {
      currentCard: 0,
      finished: false
    }
  },
  methods: {
    popuped () {
      this.$store.commit('setState', ['learningMode', false])
      this.currentCard = 0
      this.finished = false
    },
    next () {
      if (this.currentCard !== this.cards.length - 1) {
        var img = document.getElementById('card-img')
        if (img) {
          img.src = ' '
          img.classList.add('ajax-loader')
        }
        this.currentCard++
      } else this.finished = true
    }
  }
}
</script>



