import axios from 'axios'
var myMixin = {
  data () {
    return {
      axios: axios
    }
  },
  computed: {
    userDB () {
      return this.$store.getters.userDB
    },
    allwords () {
      return this.$store.getters.allwords
    },
    language () {
      return this.$store.getters.language
    }
  },
  methods: {
    updateWords () {
      var allwordsss = []
      for (let i = 0; i < this.userDB.collections.length; i++) {
        for (let j = 0; j < this.userDB.collections[i].knowledges.length; j++) {
          allwordsss.push(this.userDB.collections[i].knowledges[j])
        }
      }
      this.$store.commit('setState', ['allwords', allwordsss])
    },
    loadDB () {
      axios.get('/get-db')
      .then((response) => { // response.data
        console.log('loadDB()')
         // commit     ,          . let lang = req.headers["accept-language"].split(",")[0]
        if (!this.userDB.login) {
          if (response.data === 'ru') {
            console.log('Russified')
            this.$store.commit('setState', ['language', this.$store.getters.ru])
          } else {
            console.log('Englified')
            this.$store.commit('setState', ['language', this.$store.getters.en])
          }
        } else {
          if (this.userDB.language === 'ru') {
            this.$store.commit('setState', ['language', this.$store.getters.ru])
          } else {
            this.$store.commit('setState', ['language', this.$store.getters.en])
          }
        }
        this.userDB.login ? this.$store.commit('setState', ['isLogged', true]) : this.$store.commit('setState', ['isLogged', false])
        // console.log(response.data.collections[0].knowledges)
        this.$store.commit('setState', ['userDB', response.data])
        this.updateWords()
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
export default myMixin



