// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import Navbar from './components/Navbar'
import Words from './components/Words'
import Jumbotron from './components/Jumbotron'
import List from './components/List'
import CollSelector from './components/Coll-selector'
import CollCreating from './components/Coll-creating'
import CollEditing from './components/Coll-editing'
import Card from './components/Card'

import globalMixins from './mixins/Globalmixins'
import store from './store/store'

Vue.component('list', List)
Vue.component('navbar', Navbar)
Vue.component('words', Words)
Vue.component('jumbotron', Jumbotron)
Vue.component('collselector', CollSelector)
Vue.component('collcreating', CollCreating)
Vue.component('collediting', CollEditing)
Vue.component('card', Card)

Vue.mixin(globalMixins)

Vue.config.productionTip = false

/* eslint-disable no-new */

new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



