<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>

export default {
  name: 'app',
  mounted () {
    this.loadDB()
  },
  created () {
    this.$store.commit('setState', ['language', this.$store.getters.ru])
    this.loadDB()
  }
}
</script>

<style lang="scss"> // main styles
  button,button:focus,button:active {
    text-decoration: none !important;
    outline:none !important;
    box-shadow: none !important;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0) !important;
  }
  $bootstrap-social: 'https://cdn.jsdelivr.net/gh/lipis/bootstrap-social@5.1.1/bootstrap-social.css';
  @import '~bootstrap/scss/_functions';
  @import '~bootstrap/scss/_variables';
  @import '~bootstrap/scss/mixins/_breakpoints';
  @import url($bootstrap-social);

  @mixin orange-gradient() { //238 142 104 1  106 58 96 1  13 22 46 1 rgb(238,142,104) rgb(106,58,96) rgb(13,22,46)
    background: -moz-linear-gradient(-45deg,  rgba(255,183,0,0.83) 0%, rgba(247,89,63,0.8) 99%, rgba(247,88,64,0.8) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg,  rgba(255,183,0,0.83) 0%,rgba(247,89,63,0.8) 99%,rgba(247,88,64,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg,  rgba(255,183,0,0.83) 0%,rgba(247,89,63,0.8) 99%,rgba(247,88,64,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  }
  @mixin my-btn($color) {
    border-color:$color;
    font-weight: 500;
    color:$color;
    font-size: 17px;
    background: #fff;
    &:hover {
      background: $color;
      color:#fff;
    }
  }
  @mixin active-btn($color) {
    background: $color;
    color:#fff;
  }
  @media (max-width:321px){ // <320px optimization for jumbotron title
    .display-4{
      font-size: 2.5rem
    }
  }
  @include media-breakpoint-down(xs) {
    .list-view{
      margin: 0 5px;
    }
    .collectionInteract-view {
      margin: 0 5px;
      padding-left: 5px;
      padding-right: 5px;
      button.my-btn-confirm{
        margin-left: 4px !important
      }
    }
    .collectionCreating-view {
      margin-left: 5px;
      margin-right: 5px;
    }
    .learning-card {
      margin-left: 5px;
      margin-right: 5px;
    }
  }

  .jumbotron {
    @include orange-gradient;
  }
  .input-group {
    margin-bottom: 25px ;
  }
  .my-header {
    margin-bottom: 25px;
  }
  .my-btn-confirm {
    @include my-btn(#4dbb2c); /*#75da8b*/
  }
  .my-btn-orange {
    @include my-btn(#ffb700);
    &:hover{
      @include orange-gradient;
    }
  }
  .my-btn-orange-active {
    @include orange-gradient;
    color:#fff;
  }
  .my-btn-red { //rgb(248, 124, 97)
    @include my-btn(rgb(248, 124, 97));  //#d9534f - bootstrap bg-danger
  }
  .display-5 {
    font-size: 1.4em;
    color: rgb(43, 42, 42);
    word-wrap: break-word;
  }
  .lead {
     font-size:24px;
     opacity:.7
  }
  input.form-control {
    &:focus{
      border-color:#ffb700
    }
  }
  .btn-social  {
    color:#fff !important;
  }
  .my-list-checkbox {
    cursor:pointer
  } //#ffb700
  .collectionInteract-view input {
    border-top-right-radius: 0.25rem !important;
    border-bottom-right-radius: 0.25rem !important
  }
  .bg-danger {
    background-color: rgb(248, 124, 97) !important
  }
</style>



