<template lang="html">
  <div class="check-test-view">
    <navbar></navbar>
    <jumbotron><center><h1 class="display-4">{{language.checkTitle}}</h1></center></jumbotron>
    <collselector v-if="!testMode && !finished" :fromTest='true'></collselector>
    <card v-if="testMode && !finished" :fromTest='true' :currentCard='currentCard'>
      <center><button type="button" class="btn my-btn-confirm" @click='nextCard()'>{{language.btnNext}}</button></center>
    </card>
    <div class="container" v-if="finished">
      <p class="display-5" style="text-align:center">{{ language.checkEnd1 + correct + language.checkEnd2 + cards.length}}</p>
      <list :fromTest="true"></list>
    </div>
  </div>
</template>

<script>
import SMmixin from '../../mixins/StateManagment.js'
export default {
  mixins: [SMmixin],
  data () {
    return {
      currentCard: 0,
      correct: 0,
      anwser: '',
      finished: false
    }
  },
  methods: {
    nextCard () {
      if (this.currentCard < this.cards.length - 1) {
        if (this.cards[this.currentCard].key === this.anwser) {
          this.correct++
          this.cards[this.currentCard].passed = true            // working, but need commition after
        } else this.cards[this.currentCard].passed = false
        this.anwser = ''
        this.currentCard++
      } else {
        if (this.cards[this.currentCard].key === this.anwser) {
          this.correct++
          this.cards[this.currentCard].passed = true
        } else this.cards[this.currentCard].passed = false
        this.finished = true
        this.$store.commit('setState', ['testMode', false])
      }
    }
  }
}
</script>

<style lang="scss">
</style>



