<template lang="html">
  <div class="list-view container">
    <!-- for the all words -->
    <ul class="list-group" v-if="this.collection === 'All' && !fromTest">
      <div class="empty-list container" style="text-align:center; " v-if="allwords.length === 0">
        <p class="display-5" style="margin-bottom:22px">{{language.emptyColl}}</p>
      </div>
      <li v-else v-for="item in allwords" class="list-group-item row "  @click="(checkboxMode ? select($event) : null)" :class="{'my-list-checkbox ' : checkboxMode}" style=""  >
        <div class="vertical-line" ></div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.key}}</p>
        </div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.value}}</p>
        </div>
      </li>
    </ul>
    <!-- for the collections -->
    <ul class="list-group" v-else-if="!fromTest">
      <div class="empty-list container" style="text-align:center; " v-if="this.userDB.collections[this.collection].knowledges.length === 0">
        <p class="display-5" style="margin-bottom:22px">{{language.emptyColl}}</p>
      </div>
      <li v-else v-for="(item, index) in this.userDB.collections[this.collection].knowledges" class="list-group-item row "  @click="(checkboxMode ? select($event) : null)" :class="{'my-list-checkbox ' : checkboxMode}" style=""  >
        <div class="vertical-line" ></div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.key}}</p>
        </div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.value}}</p>
        </div>
      </li>
    </ul>
    <!-- for the test mode RESULT -->
    <ul class="list-group" v-else-if="fromTest">
      <li v-for="(item, index) in this.cards" class="list-group-item row "  :class="{'list-group-item-success' : item.passed, 'list-group-item-danger' : !item.passed}" style=""  >
        <div class="vertical-line" ></div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.key}}</p>
        </div>
        <div class="col-6">
          <p class="display-5" style="margin-bottom:0">{{item.value}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import SMmixin from '../mixins/StateManagment.js'
export default {
  mixins: [SMmixin],
  props: ['checkboxMode', 'fromTest'],
  methods: {
    select (event) {
      if (this.typeofEdit === 'add') {
        let Class = 'bg-success'
        event.currentTarget.classList.contains(Class)
          ? event.currentTarget.classList.remove(Class)
          : event.currentTarget.classList.add(Class)
      } else if (this.typeofEdit === 'remove') {
        let Class = 'bg-danger'
        event.currentTarget.classList.contains(Class)
          ? event.currentTarget.classList.remove(Class)
          : event.currentTarget.classList.add(Class)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~bootstrap/scss/_functions';
@import '~bootstrap/scss/_variables';
@import '~bootstrap/scss/mixins/_breakpoints';

.list-group-item-success .col-6 p {
  color: #3c763d
}

.list-group-item-danger .col-6 p {
  color: #a94442
}


.bg-warning {
  background-color: #ffb700 !important
}

.bg-warning .col-6 p {
  color: #fff;
  // font-size: 1.3em
}
.bg-success .col-6 p {
  color: #fff;
  // font-size: 1.3em
}
.bg-danger .col-6 p {
  color: #fff;
  // font-size: 1.3em
}
.vertical-line{
  position:absolute;
  border-right: 1px solid rgba(0, 0, 0, 0.125);
  height: 100%;
  left: 50%;
}
</style>



