<template>
  <div class="words-view container">

    <div class="beginning" v-if="!logstatus">
      <center><h1 class="my-header display-4">{{language.logoutStart}}</h1></center>
      <p class="display-5" style ="text-align:center">{{language.logoutSignUp}}</p> <!-- Simly : "Sign up"  -->
      <!-- Sign up form here -->
      <center style="margin-bottom:20px">
        <a class="btn btn-social btn-google" href="/auth/google">
          <span class="fa fa-google"></span> {{language.logoutGoogle}}
        </a>
      </center>
    </div>

    <div class="authorized" v-else>
      <p class="display-5">{{language.wordsTitleLogin}}</p>
      <!-- rgb(255, 183, 0) rgb(43, 42, 42) -->
      <div class="row">
        <div class="col-lg-6">
          <div class="input-group">
            <span class="input-group-addon" style="background:rgb(107, 107, 107);color:#fff;">{{language.wordsInput1}}</span>
            <input type="text" class="form-control" v-model="word_key">
          </div>
        </div>
        <div class="col-lg-6">
          <div class="input-group">
            <span class="input-group-addon" style="background:rgb(107, 107, 107);color:#fff;">{{language.wordsInput2}}</span>
            <input type="text" class="form-control" v-model="word_value">
          </div>
        </div>
      </div>
      <center>
        <button type="button" class="btn my-btn-confirm" @click="addCouple()">{{language.btnConfirm}}</button>
        <router-link to="collections" tag="button" type="button" class="btn my-btn-orange" >{{language.btnGo}}</router-link>
      </center>
    </div>

  </div>
</template>



<script>
import SMmixin from '../mixins/StateManagment.js'

export default {
  mixins: [SMmixin],
  name: 'words',
  data () {
    return {
      word_key: '',
      word_value: '',
      headertext: 'User'
    }
  },
  methods: {
    addCouple () {
      this.axios.post('/add-couple', {
        word_key: this.word_key,
        word_value: this.word_value
      })
      .then((response) => {
        this.word_key = ''
        this.word_value = ''
        this.loadDB()
      })
    }
  }

}
</script>

<style lang="scss" >
</style>



