<template lang="html">
  <div class="collections-view">
    <navbar></navbar>
    <jumbotron><center><h1 class="display-4">{{language.manageTitle}}</h1></center></jumbotron>
    <!-- <button type="button" class="btn my-btn-red" @click="getImage()">Request image</button> -->
    <collselector :fromEditor="true"></collselector>
    <transition name="slide-fade">
      <collcreating v-if="addMode" ></collcreating>
    </transition>
    <collediting></collediting>
    <list :checkboxMode="true" ></list>
  </div>
</template>

<script>
import SMmixin from '../../mixins/StateManagment.js'

export default {
  mixins: [SMmixin]
}
</script>

<style lang="scss" scoped>
  @import '~bootstrap/scss/_functions';
  @import '~bootstrap/scss/_variables';
  @import '~bootstrap/scss/mixins/_breakpoints';
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active   2.1.8 */ {
    transform: translateY(-40px);
    opacity: 0;
  }
</style>



