<template lang="html">
  <div class="collectionCreating-view container transition" >
    <div class="input-group">
      <span class="input-group-addon" style="background:rgb(107, 107, 107);color:#fff;" >{{language.addCollName}}</span>
      <input type="text" class="form-control" v-model="newcollName">
      <button type="button" class="btn my-btn-confirm" @click="createCollection()" style="margin-left:10px">{{language.btnAdd}}</button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      newcollName: ''
    }
  },
  methods: {
    createCollection () {
      this.axios.post('/create-collection', {
        collname: this.newcollName
      }).then(() => {
        this.loadDB()
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '~bootstrap/scss/_functions';
@import '~bootstrap/scss/_variables';
@import '~bootstrap/scss/mixins/_breakpoints';

.collectionCreating-view {
  padding-top: 20px;
  margin-bottom: 10px;
  border: 1px solid rgb(77, 187, 44);
  border-radius: .25rem;

}

</style>



