var stateManagment = {
  computed: {
    collection () {
      return this.$store.getters.collection
    },
    logstatus () {
      return this.$store.getters.logstatus
    },
    addMode () {
      return this.$store.getters.addMode
    },
    editMode () {
      return this.$store.getters.editMode
    },
    typeofEdit () {
      return this.$store.getters.typeofEdit
    },
    cards () {
      return this.$store.getters.cards
    },
    learningMode () {
      return this.$store.getters.learningMode
    },
    testMode () {
      return this.$store.getters.testMode
    }
  },
  methods: {
    clearSelected () {
      if ((document.getElementsByClassName('bg-success').length > 0) || (document.getElementsByClassName('bg-danger').length > 0)) {
        let selectedlist
        if (document.getElementsByClassName('bg-success').length > 0) {
          selectedlist = document.getElementsByClassName('bg-success')
          while (selectedlist.length > 0) {
            selectedlist[0].classList.remove('bg-success')
          }
        } else {
          selectedlist = document.getElementsByClassName('bg-danger')
          while (selectedlist.length > 0) {
            selectedlist[0].classList.remove('bg-danger')
          }
        }
      }
      this.$store.commit('setState', ['typeofEdit', ''])
    },
    changeCollection (index) {
      this.clearSelected()
      this.$store.commit('setState', ['collection', index])
    },
    changeStatus (status) {
      this.$store.commit('setState', ['isLogged', status])
    },
    toggleMode (state) {
      this.clearSelected()
      this.$store.commit('toggle', [state])
    },
    setType (type) {
      this.clearSelected()
      this.$store.commit('setState', ['typeofEdit', type])
    }
  }
}
export default stateManagment



