<template lang="html">
  <div class="collectionEditing-view container" v-if="this.collection !== 'All' && !(this.collection === 0 && this.userDB.collections[0].knowledges.length === 0)">
    <!-- <div class="container editcoll" style="text-align:center; ">
      <hr>
      <p class="display-4" style="margin-bottom:22px">Edit collection</p>
      <hr>
    </div> -->
    <div class="row" style="margin-bottom: 10px" v-if="typeofEdit === 'add'">
      <div class="col-12" style="margin-bottom: 4px; text-align: center">
        <button type="button" class="btn my-btn-orange" @click="updateCollection(newcollname)" >{{language.btnSelAdd}}</button>
      </div>
    </div>
    <div class="row" style="margin-bottom: 10px" v-else-if="typeofEdit === 'remove'">
      <div class="col-12" style="margin-bottom: 4px; text-align: center">
        <button type="button" class="btn my-btn-orange" @click="updateCollection(newcollname)" >{{language.btnSelRemove}}</button>
      </div>
    </div>
    <div class="row" style="margin-bottom: 10px"  id="coll-less" v-else>
      <div class="col-sm-6" style="margin-bottom: 4px" v-if="this.collection !== 0" :class="{ 'col-sm-12' : userDB.collections[collection].knowledges.length === 0}">
        <button type="button" class="btn my-btn-confirm" @click="editListen('add')" :class="{ 'my-btn-confirm-active' : typeofEdit === 'add' }" >{{language.btnAddKnows}}</button>
      </div>
      <div v-if="userDB.collections[collection].knowledges.length > 0" :class="{ 'col-sm-12' : this.collection === 0 , 'col-sm-6' : this.collection !== 0 }">
        <button type="button" class="btn my-btn-red" @click="editListen('remove')" :class="{ 'my-btn-red-active' : typeofEdit === 'remove' }">{{language.btnRemoveKnows}}</button>
      </div>
    </div>
  </div>
</template>

<script>
import SMmixin from '../mixins/StateManagment.js'
export default {
  mixins: [SMmixin],
  data () {
    return {
      newcollname: '',
      selectedArr: []
    }
  },
  methods: {
    editListen (type) {
      if (type === 'add') {
        if (this.typeofEdit === 'add') {
          this.setType('')
        } else {
          this.setType(type)
          this.newcollname = this.userDB.collections[this.collection].name
          this.$store.commit('setState', ['collection', '0'])
        }
      } else {
        if (this.typeofEdit === 'remove') {
          this.setType('')
        } else {
          this.setType(type)
        }
      }
    },
    updateCollection (collname) {
      let selectedlist
      this.typeofEdit === 'add'
        ? selectedlist = document.getElementsByClassName('bg-success')
        : selectedlist = document.getElementsByClassName('bg-danger')

      let list = document.getElementsByTagName('li')
      // console.log(' : ' + list.length)
      // console.log(': ' + selectedlist.length)
      this.selectedArr = []
      // console.log('Selected list: ' + JSON.stringify(selectedlist[0]))
      for (let i = 0; i < selectedlist.length; i++) {
        for (let j = 0; j < list.length; j++) { // because findIndex() isnt supported in chrome
          if (selectedlist[i].outerHTML === list[j].outerHTML) {
            // console.log('list outerHTML: ' + list[j].outerHTML)
            // console.log('selectedList outerHTML: ' + selectedlist[i].outerHTML)
            if (this.userDB.collections[this.collection].knowledges[j].img.length > 2 || this.typeofEdit === 'remove') {  // check if word have googled
              this.selectedArr.push(this.userDB.collections[this.collection].knowledges[j])
              j = list.length - 1
            }
          }
        }
      }
      // console.log('Deleted list: ' + JSON.stringify(this.selectedArr))
      this.axios.post('/update-collection', {
        collname: collname,
        ids: this.selectedArr,
        type: this.typeofEdit
      }).then(() => {
        this.loadDB() //  ,  
        this.setType('')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  @import '~bootstrap/scss/_functions';
  @import '~bootstrap/scss/_variables';
  @import '~bootstrap/scss/mixins/_breakpoints';

  .row :first-child{
    text-align: right;
  }

  @include media-breakpoint-down(xs) {
    .row .col-sm-6{
      text-align: center;
    }
  }
  .editcoll p{
    @include media-breakpoint-down(xs) {
      font-size: 2.8rem
    }
  }
  .editcoll hr{ //rgb(107, 107, 107) rgba(0, 0, 0, 0.26) #ffb800
    border-color: rgb(107, 107, 107)
  }
  #coll-less .col-sm-12{
    text-align: center !important
  }
</style>



