<template lang="html">
  <div class="navbar-background">
    <nav class="navbar navbar-toggleable-sm navbar-light navbar-light container" id="nav-cont">
        <div class="row " style="min-width: 100%;padding:0 16px">
          <div class="title col-6" style="padding:0">
            <h1 style="margin-bottom:0;font-weight:300;color: rgb(107, 107, 107); white-space:nowrap">The Learnity</h1>
          </div>
          <div class=" col-6" style="padding:0">
            <!-- Toggler -->
            <div class="justify-content-end" style="display:flex">
              <button class="navbar-toggler " data-toggle="collapse" data-target="#mainNav" >
              <span class="navbar-toggler-icon " ></span>
              </button>
            </div>
            <!-- Navbar -->
            <div class="collapse navbar-collapse justify-content-end" id="mainNav">
              <div class="nav navbar-nav" style="text-align: right">
                <router-link to="/" class="nav-item nav-link">{{language.home}}</router-link>
                <router-link to="collections" class="nav-item nav-link" v-if="this.logstatus">{{language.collections}}</router-link>
                <router-link to="manage" class="nav-item nav-link" v-if="this.logstatus">{{language.manage}}</router-link>
                <router-link to="learning" class="nav-item nav-link" v-if="this.logstatus">{{language.learning}}</router-link>
                <router-link to="check" class="nav-item nav-link" v-if="this.logstatus">{{language.check}}</router-link>
                <router-link to="/" @click.native="logout()" class="nav-item nav-link"  v-if="this.logstatus" style="cursor:pointer">{{language.logout}}</router-link>
              </div>
            </div>
          </div>
        </div>
    </nav>
  </div>
</template>

<script>
import SMmixin from '../mixins/StateManagment.js'

export default {
  mixins: [SMmixin],
  name: 'navbar',
  methods: {
    logout () {
      this.axios.get('/auth/logout')
      .then((response) => {
        this.changeStatus(false)
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>

<style lang="css" scoped>
@media (max-width:400px) {
  .title h1{
    font-size: 1.9em;
  }
}
@media (max-width:576px) {
  #nav-cont {
    margin: 0 0;
  }
}
.navbar-nav a{
  white-space: nowrap;
}
</style>



