<template lang="html">
  <div class="my-card" >
    <div class="container learning-card" >

      <div class="picture">
        <center>
          <img v-if="cards[currentCard].img !== 'null' " id="card-img" :src="cards[currentCard].img" class="load ajax-loader" alt="" onload="this.classList.remove('ajax-loader')">
          <p v-else class="display-5" style="margin-top:16px">Sorry, we couldn't find any image</p>
        </center>
      </div>

      <div class="couple" v-if="!fromTest">
        <div class="vertical-line"></div>
        <div class="row" style="padding: 20px 0px; max-width:100%">
          <div class="col-6">
            <p class="display-5" style="margin-bottom:0">{{cards[currentCard].key}}</p>
          </div>
          <div class="col-6" >
            <p class="display-5" style="margin-bottom:0">{{cards[currentCard].value}}</p>
          </div>
        </div>
      </div>

      <div class="couple" v-else>
        <div class="vertical-line"></div>
        <div class="row" style="padding: 20px 0px; max-width:100%">
          <div class="col-6">
            <input type="text" class="form-control" name="" value="" v-model="$parent.anwser" placeholder="Write correct word">
          </div>
          <div class="col-6" >
            <p class="display-5" style="margin-bottom:0">{{cards[currentCard].value}}</p>
          </div>
        </div>
      </div>
    </div>
    <slot></slot>
    <!-- <center><button :data-toggle="finished ? 'modal' : null" data-target="#myModal" type="button" class="btn my-btn-confirm" @click="() => { if (currentCard !== cards.length-1) currentCard++; else finished = true}">Got it <i class="fa fa-long-arrow-right" aria-hidden="true" style="margin-left:5px"></i></button></center> -->
  </div>
</template>

<script>
import SMmixin from '../mixins/StateManagment.js'
export default {
  props: ['currentCard', 'fromTest'],
  mixins: [SMmixin]
}
</script>

<style lang="scss">
  #card-img{
    max-width:100%;
    min-width: 100px;
    min-height: 100px;
  }
  .ajax-loader{
    background-image: url('/static/loader.svg');
    background-repeat: no-repeat;
    background-position:center;
  }

  .vertical-line{
    position:absolute;
    border-right: 1px solid rgba(0, 0, 0, 0.125);
    height: 100%;
    left: 50%;
  }
  .picture{
    // padding: 10px;
    img {
      border-top-right-radius: .25rem;
      border-top-left-radius: .25rem;
    }
  }
  .learning-card{
    padding: 0px;
    border: 1px solid rgba(0, 0, 0, 0.125);
    border-radius: .25rem;
    margin-bottom: 10px;
  }
  .couple{
    border-top: 1px solid rgba(0, 0, 0, 0.125);
    position: relative;
    .row {
      margin-left: 0px !important;
      margin-right: 0px !important;
    }
  }
  .modal-dialog {
    min-height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: auto;
    @media(max-width: 768px) {
      min-height: calc(100vh - 20px);
    }
  }
  .modal-title{
    font-weight: normal;
  }
  .load {
    // background: url('http://survivalprepgroup.com/wp-content/uploads/2017/08/loading.gif') no-repeat center;
  }
</style>



